/* STATIC STYLES (desktop-first) */

.hero {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-image: url('../../../public/hero_background.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.hero-content {
  position: absolute;
  top: 8rem;
  left: 50%;
  transform: translateX(-50%);
  padding: 0 2rem;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 2rem;
  z-index: 2;
}

.hero-marker {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-marker .dot {
  width: 20px;
  height: 20px;
  background-color: var(--color-secondary-dark);
  border-radius: 50%;
}

.hero-marker .line {
  width: 4px;
  height: 320px;
  background: linear-gradient(to bottom, var(--color-secondary-dark), transparent);
}

.hero-text h1 {
  color: var(--color-primary-text);
  white-space: nowrap;
}

.hero-text .highlight {
  color: var(--color-secondary);
}

.hero-text h3 {
  margin-top: 0.5rem;
  color: var(--color-primary-text-dark);
  white-space: nowrap;
}

.scroll-indicator {
  position: absolute;
  bottom: 2rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.scroll-box {
  width: 35px;
  height: 64px;
  border-radius: 24px;
  border: 4px solid var(--color-primary-text-dark);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 8px;
  position: relative;
}

.scroll-dot {
  width: 12px;
  height: 12px;
  background-color: var(--color-primary-text-dark);
  border-radius: 50%;
  animation: bounce 1.5s infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(24px);
  }
}

